.card {
  width: 327px;
}
@media (min-width: 59.375em) {
  .card {
    width: 912px;
  }
}
.card .column__sedans {
  background-color: var(--bright-orange);
}
.card .column__suvs {
  background-color: var(--dark-cyan);
}
.card .column__luxury {
  background-color: var(--very-dark-cyan);
}
.card .column__icon {
  margin-bottom: 2.5rem;
}
.card .column__title {
  color: var(--white);
  font-family: var(--font-big-shoulders);
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2.1875rem;
  letter-spacing: 0.5px;
  margin-bottom: 40px;
}
.card .column__detail {
  color: var(--transparent-white);
  font-weight: 400;
  font-size: 15px;
  line-height: 1.8;
  margin-bottom: 30px;
}
.card .column__button {
  border: 2px solid var(--very-light-gray);
  background: var(--very-light-gray);
  display: inline-block;
  padding: 16px 32px;
  border-radius: 30px;
}
.card .column__button--sedan {
  color: var(--bright-orange);
}
.card .column__button--sedan:hover {
  background: var(--bright-orange);
  color: var(--very-light-gray);
}
.card .column__button--suv {
  color: var(--dark-cyan);
}
.card .column__button--suv:hover {
  background-color: var(--dark-cyan);
  color: var(--very-light-gray);
}
.card .column__button--luxury {
  color: var(--very-dark-cyan);
}
.card .column__button--luxury:hover {
  background: var(--very-dark-cyan);
  color: var(--very-light-gray);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: var(--background);
  font-family: var(--font-lexend);
  font-size: 16px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(179, 100%, 13%);
}

:root {
  --bright-orange: hsl(31, 77%, 52%);
  --dark-cyan: hsl(184, 100%, 22%);
  --very-dark-cyan: hsl(179, 100%, 13%);
  --transparent-white: hsla(0, 0%, 100%, 0.75);
  --very-light-gray: hsl(0, 0%, 95%);
  --background: var(--very-light-gray);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-lexend: "Lexend Deca", sans-serif;
  --font-big-shoulders: "Big Shoulders Display", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main {
  border-radius: 8px;
  overflow: hidden;
}

.container {
  padding: 47px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}